import React from 'react';
import './Project.css';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import LinkIcon from '@material-ui/icons/Link';
import LaunchIcon from '@material-ui/icons/Launch';
import Chip from '@material-ui/core/Chip';
const useStyles = makeStyles({
  root: {
    maxWidth: 400,
    margin: 20,
    borderRadius: 10,
    boxShadow: '0px 24px 14px -3px rgba(199,199,199,1)',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
  },
  media: {
    paddingTop: '56.25%',
    objectFit: 'cover',
    backgroundPosition: 'left',
  },
  button: {
    width: '50%',
    padding: '1rem',
  },
  link: {
    marginLeft: 8,
  },
});

const Project = ({
  image,
  title,
  description,
  liveDemo,
  githubLink,
  projectTags,
}) => {
  const classes = useStyles();
  return (
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia className={classes.media} image={image} title={title} />
        <CardContent className='project__content'>
          <h2>{title}</h2>
          <p>{description}</p>
          {projectTags ? (
            <div className='project__labels'>
              {projectTags.map((tag) => (
                <Chip label={tag} />
              ))}
            </div>
          ) : null}
        </CardContent>
      </CardActionArea>
      <div className='project__buttons'>
        <Button
          className={classes.button}
          size='small'
          color='primary'
          rel='noopener noreferrer'
          target='_blank'
          href={liveDemo}
        >
          live demo <LaunchIcon className='project__icon pulse' />
        </Button>
        <Button
          className={classes.button}
          size='small'
          color='primary'
          rel='noopener noreferrer'
          target='_blank'
          href={githubLink}
        >
          github <LinkIcon className={classes.link} />
        </Button>
      </div>
    </Card>
  );
};

export default Project;
