import React from 'react'
import './About.css'
import SectionHeader from './SectionHeader'
const About = () => {
    return (
        <section className="about" id='about'>
            <div className="about__container">
                <SectionHeader title='About' color='white'/>
                
                <div className="about__desciption">
                    <p>My name is Sami Ahmad, I am a Full Stack ReactJS and Node.js developer.  In my free time I like to make projects that push me out of my comfort zone, so that I can learn something new everyday. </p>
                    <div className='about__contact'>samiahmad.dev@gmail.com</div>
                </div>
            </div>
        </section>
    )
}

export default About
