import React from 'react';
import './Projects.css';
import Fade from 'react-reveal/Fade';
import Project from './Project';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import SectionHeader from './SectionHeader';
// Project images
import NetflixClone from './images/netflix.jpg';
import AirBnbClone from './images/airbnb.jpg';
import TwitterClone from './images/twitter.jpg';
import AmazonClone from './images/amazon.jpg';
import RedditClone from './images/reddit.jpg';
import SpotifyClone from './images/spotify.jpg';
import VacationClone from './images/vacation.jpg';

const Projects = () => {
  return (
    <section className='projects' id='project'>
      <div className='projects__container'>
        <SectionHeader title={'PROJECTS'} />
        <Container maxWidth='xl'>
          <Grid container spacing={3} className='projects__grid'>
            <Project
              image={NetflixClone}
              title={'Netflix Clone'}
              description={
                'Netflix clone built using ReactJS and TMDB database API'
              }
              liveDemo={'https://netflix-clone-b21fc.web.app/'}
              githubLink={'https://github.com/1ahmadsam/netflix-clone'}
              projectTags={['ReactJS', 'Firebase', 'Material-UI']}
            />
            <Project
              image={AirBnbClone}
              title={'AirBNB Clone'}
              description={
                'AirBNB clone build using ReactJS with React router, and MaterialUI'
              }
              liveDemo={'https://airbnb-clone-f7892.web.app/'}
              githubLink={'https://github.com/1ahmadsam/airbnb-clone'}
              projectTags={['ReactJS', 'Firebase', 'Material-UI']}
            />
            <Project
              image={TwitterClone}
              title={'Twitter Clone'}
              description={
                'Twitter Clone build using Javascript, and Firebase hosting'
              }
              liveDemo={'https://twitter-clone-d1d7c.web.app/'}
              githubLink={'https://github.com/1ahmadsam/twitter-clone'}
              projectTags={['Javascript', 'Firebase', 'Material-UI']}
            />
            <Project
              image={VacationClone}
              title={'VacationList App'}
              description={
                'Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging across all continents except Antarctica'
              }
              liveDemo={'https://vacation-list-59e8b.web.app/'}
              githubLink={'https://github.com/1ahmadsam/VacationList'}
              projectTags={['Javascript', 'Firebase']}
            />
            <Project
              image={AmazonClone}
              title={'Amazon Clone'}
              description={
                'Amazon clone build using ReactJS, NodeJS + Express, Stripe Payment, Material UI, Context API, Firebase Hosting, Authentication and Functions'
              }
              liveDemo={'https://clone-5e662.web.app/'}
              githubLink={'https://github.com/1ahmadsam/amazon-clone'}
              projectTags={[
                'ReactJS',
                'Node.js',
                'Firebase',
                'Stripe',
                'Authentication',
                'Context API',
                'Material UI',
              ]}
            />
            <Project
              image={SpotifyClone}
              title={'Spotify Clone'}
              description={
                'Spotify clone build using ReactJS with Spotify API, User Authentication, Material UI, State Management with Context API, Firebase Hosting'
              }
              liveDemo={'https://spotify-clone-aa032.web.app/'}
              githubLink={'https://github.com/1ahmadsam/spotify-clone'}
              projectTags={[
                'ReactJS',
                'Firebase',
                'Authentication',
                'Material UI',
              ]}
            />

            <Project
              image={RedditClone}
              title={'Reddit Clone'}
              description={
                'Reddit clone build using ReactJS with Reddit API, Material UI, State Management with Context API, Firebase Hosting'
              }
              liveDemo={'https://reddit-clone-e0296.web.app/'}
              githubLink={'https://github.com/1ahmadsam/reddit_clone'}
              projectTags={['ReactJS', 'Firebase', 'Material UI']}
            />
          </Grid>
        </Container>
      </div>
    </section>
  );
};

export default Projects;
