import React, { useState } from 'react';
import './Navbar.css';
import MenuIcon from '@material-ui/icons/Menu';
import Avatar from '@material-ui/core/Avatar';
import sami from './images/sami.jpg';
import { Link, animateScroll as scroll } from 'react-scroll';
import { Button } from '@material-ui/core';

const Navbar = () => {
  const [anchorEl, setAnchorEl] = useState(false);

  const handleDrawerToggle = () => {
    setAnchorEl((anchorEl) => !anchorEl);
  };
  return (
    <>
      <nav className='navbar'>
        <div className='navbar__mobile'>
          <h1>Sami Ahmad</h1>
          <Button onClick={handleDrawerToggle} variant='contained'>
            <MenuIcon />
          </Button>
        </div>
        <ul className='navbar__nav'>
          <Avatar className='navbar__profile' alt='Sami Ahmad' src={sami} />
          <li className='navbar__item'>
            <Link
              className='navbar__link'
              activeClass='navbar__selected'
              to='home'
              spy={true}
              smooth={true}
              duration={500}
            >
              Home
            </Link>
          </li>

          <li className='navbar__item'>
            <Link
              className='navbar__link'
              activeClass='navbar__selected'
              to='about'
              spy={true}
              smooth={true}
              duration={500}
            >
              About
            </Link>
          </li>
          <li className='navbar__item'>
            <Link
              className='navbar__link'
              activeClass='navbar__selected'
              to='project'
              spy={true}
              smooth={true}
              duration={500}
            >
              Projects
            </Link>
          </li>
          <li className='navbar__item'>
            <Link
              className='navbar__link'
              activeClass='navbar__selected'
              to='work'
              spy={true}
              smooth={true}
              duration={500}
            >
              Work
            </Link>
          </li>

          <li className='navbar__item'>
            <Link
              className='navbar__link'
              activeClass='navbar__selected'
              to='contact'
              spy={true}
              smooth={true}
              duration={500}
            >
              Contact
            </Link>
          </li>
        </ul>
      </nav>
      <div
        className={
          anchorEl
            ? 'navbar__mobileCollapse'
            : 'navbar__mobileCollapse collapse'
        }
      >
        <ul>
          <li className='navbar__item'>
            <Link
              className='navbar__mobileLink'
              activeClass='navbar__selected'
              to='home'
              spy={true}
              smooth={true}
              duration={500}
            >
              Home
            </Link>
          </li>

          <li className='navbar__item'>
            <Link
              className='navbar__mobileLink'
              activeClass='navbar__selected'
              to='about'
              spy={true}
              smooth={true}
              duration={500}
            >
              About
            </Link>
          </li>
          <li className='navbar__item'>
            <Link
              className='navbar__mobileLink'
              activeClass='navbar__selected'
              to='project'
              spy={true}
              smooth={true}
              duration={500}
            >
              Projects
            </Link>
          </li>
          <li className='navbar__item'>
            <Link
              className='navbar__mobileLink'
              activeClass='navbar__selected'
              to='work'
              spy={true}
              smooth={true}
              duration={500}
            >
              Work
            </Link>
          </li>

          <li className='navbar__item'>
            <Link
              className='navbar__mobileLink'
              activeClass='navbar__selected'
              to='contact'
              spy={true}
              smooth={true}
              duration={500}
            >
              Contact
            </Link>
          </li>
        </ul>
      </div>
    </>
  );
};

export default Navbar;
