import React from 'react';
import './Work.css';

import SectionHeader from './SectionHeader';
const Work = () => {
  return (
    <section className='work' id='work'>
      <SectionHeader title='experience' />
      <div className='work__container'>
        <div className='work__item'>
          <div className='work__description'>
            <h3>French Translation Integration</h3>
            <ul className='work__points'>
              <li>
                Integrated french translations on Canadian website to help reach
                customers that are french or english in .NET
              </li>
              <li>
                Explored the capabilities of a tool that allows globalization of
                websites using custom resources
              </li>
              <li>
                <b>Technologies: .NET Framework, C#</b>
              </li>
            </ul>
          </div>
          <div className='work__date'>
            <h4>Dec 2019 - Jan 2020</h4>
          </div>
        </div>
        <div className='work__item'>
          <div className='work__description'>
            <h3>Reservation Transactions - Automation System</h3>
            <ul className='work__points'>
              <li>
                Co Developed an Automation System, a tool that is used to grab
                all transaction details from a date the user decides on and
                merges them
              </li>
              <li>
                Created the user flow required to allow the program to find the
                specific reservation and extract the information using Python
                and Scrapy
              </li>
              <li>
                <b>Technologies: Python, Scrapy, Selenium</b>
              </li>
            </ul>
          </div>
          <div className='work__date'>
            <h4>Oct 2019 - Jan 2020</h4>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Work;
